:root {
  --main-color: #5865f2;
  --secondary-color: #98aae7;
  --secondary-color-r: 114;
  --secondary-color-g: 137;
  --secondary-color-b: 218;
  --background-color: #282a2e;
  --secondary-background-color: #2d2f33;
  --accent-background-color: #252629;
  --third-background-color: #202225;
  --box-background-color: #35353a;
  --light-accent-color: #675d5d;
}

input[type="file"] {
  display: none;
}

.custom-file-upload {
  color: #fff;
  width: 100%;
  text-align: center;
  cursor: pointer;
  border: 2px dashed var(--main-color);
  border-radius: 5px;
  margin: 4px 2px;
  padding-top: 32px;
  padding-bottom: 32px;
  font-size: 20px;
  text-decoration: none;
  transition-duration: .4s;
  display: inline-block;
}

.custom-file-upload svg {
  stroke: rgba(var(--secondary-color-r), var(--secondary-color-g), var(--secondary-color-b), .4);
  padding-bottom: 10px;
}

.custom-file-upload:hover {
  background-color: rgba(var(--secondary-color-r), var(--secondary-color-g), var(--secondary-color-b), .3);
}

.landingpage {
  text-align: center;
  max-width: 770px;
  margin: 0 auto;
  padding: 40px;
}

.button {
  color: #fff;
  width: 100%;
  text-align: center;
  cursor: pointer;
  border: none;
  margin: 4px 2px;
  padding: 32px;
  font-size: 20px;
  text-decoration: none;
  transition-duration: .4s;
  display: inline-block;
}

.button1 {
  background-color: rgba(var(--secondary-color-r), var(--secondary-color-g), var(--secondary-color-b), .1);
  color: var(--secondary-color);
  border-radius: 5px;
}

.button1:hover {
  background-color: rgba(var(--secondary-color-r), var(--secondary-color-g), var(--secondary-color-b), .3);
  color: #fff;
}

.clickableText {
  cursor: pointer;
}

